import React from 'react'

import { Container, Navbar, Nav, NavDropdown, Button, Form, Offcanvas } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css'; // Ensure Bootstrap CSS is imported
import useWindowSize from '../hooks/useWindowSize'; // Ensure this path is correct

import "./NavHeader.css"


export const NavHeader = () => {
 const { width } = useWindowSize();

  const getNavbarExpand = () => {
    if (width < 576) return false;   // xs
    // if (width < 768) return 'sm';    // sm
    if (width < 768) return false;    // sm
    // if (width < 992) return 'md';    // md
    if (width < 992) return false;    // md
    if (width < 1200) return 'lg';   // lg
    if (width < 1400) return 'xl';   // xl
    return 'xxl';                    // xxl
  };

  const expand = getNavbarExpand();



  return (
    
     <Navbar expand={expand} className="navbar-custom  bg-body-tertiary mb-3" bg="dark" data-bs-theme="dark">
      <Container fluid>
        <Navbar.Brand href="#">Navbar Offcanvas</Navbar.Brand>
        <Navbar.Toggle aria-controls={`offcanvasNavbar-expand-${expand}`} />
        <Navbar.Offcanvas
          id={`offcanvasNavbar-expand-${expand}`}
          aria-labelledby={`offcanvasNavbarLabel-expand-${expand}`}
          placement="end"
          bg="dark" data-bs-theme="dark"
          position="fixed"
        >
          <Offcanvas.Header closeButton>
            <Offcanvas.Title id={`offcanvasNavbarLabel-expand-${expand}`}>
              Offcanvas
            </Offcanvas.Title>
          </Offcanvas.Header>
          <Offcanvas.Body>
            <Nav className="justify-content-end flex-grow-1 pe-3">
              <Nav.Link href="#action1">Home</Nav.Link>
              <Nav.Link href="#action2">Link</Nav.Link>
              <NavDropdown
                title="Dropdown"
                id={`offcanvasNavbarDropdown-expand-${expand}`}
              >
                <NavDropdown.Item href="#action3">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action4">
                  Another action
                </NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action5">
                  Something else here
                </NavDropdown.Item>
              </NavDropdown>
            </Nav>
            <Form className="d-flex">
              <Form.Control
                type="search"
                placeholder="Search"
                className="me-2"
                aria-label="Search"
              />
              <Button variant="outline-success">Search</Button>
            </Form>
          </Offcanvas.Body>
        </Navbar.Offcanvas>
      </Container>
    </Navbar>
  )
}

export default NavHeader;