// import React, { useState } from 'react';
import React, { useState, useEffect } from 'react';
import 'video.js/dist/video-js.css';
import videojs from 'video.js';

import "./Portfolio.css"

// Import videos from the src/videos directory
import video1 from "../assets/videos/Rapid_Json.mp4";
import video2 from "../assets/videos/Vcpkg_Setup_Guide.mkv";
import video3 from "../assets/videos/Rapid_Json.mp4";
import thumbnail1 from "../assets/thumbnails/VulkanEngine_thumbnail.png";
import thumbnail2 from "../assets/thumbnails/Space_Shooter_thumbnail.png";
import thumbnail3 from "../assets/thumbnails/Branch_hopper_thumbnail.png";

// Create an array of video sources
const videos = [video1, video2, video3];


const thumbnails = [thumbnail1, thumbnail2, thumbnail3];

export const Portfolio = () => {


  const [currentIndex, setCurrentIndex] = useState(0);

  // useEffect(() => {
  //   const player = videojs('video-js', {}, () => {
  //     console.log('Player is ready');
  //   });
  //   return () => {
  //     if (player) {
  //       player.dispose();
  //     }
  //   };
  // }, [currentIndex]);

  const goToPrevious = () => {
    setCurrentIndex((prevIndex) => (prevIndex === 0 ? videos.length - 1 : prevIndex - 1));
  };

  const goToNext = () => {
    setCurrentIndex((prevIndex) => (prevIndex === videos.length - 1 ? 0 : prevIndex + 1));
  };

  const handleThumbnailClick = (index) => {
    setCurrentIndex(index);
  };


  return (
    <div className="carousel-container">
      <h1>Portfolio</h1>
      <div className="portfolio-video-wrapper">
        <video className="portfolio-video" src={videos[currentIndex]} 
        controls 
        autoPlay
        muted
        loop
        />
      </div>
      <button className="button left" onClick={goToPrevious}>Previous</button>
      <button className="button right" onClick={goToNext}>Next</button>

      <div className="thumbnails-container">
        {thumbnails.map((thumbnail, index) => (
          <div
            key={index}
            className={`thumbnail ${index === currentIndex ? 'active' : ''}`}
            onClick={() => handleThumbnailClick(index)}>
            <img src={thumbnail} alt={`Thumbnail ${index + 1}`} />
          </div>
        ))}
      </div>



  </div>
  );
}

export default Portfolio